const Menu = [
  { header: '' },
  {
    title: 'Dashboard',
    group: 'apps',
    icon: 'dashboard',
    name: 'Dashboard'
  },
  { header: '' },
  {
    title: '',
    group: 'user',
    icon: 'group',
    items: [
      { name: 'list', title: '', component: 'userList' },
      { name: 'add', title: '', component: 'components/social' }
    ]
  },
  {
    title: '',
    group: 'role',
    icon: 'people_outline',
    items: [
      { name: 'list', title: '', component: 'components/social' },
      { name: 'add', title: '', component: 'components/social' }
    ]
  },
  { header: '' },
  {
    title: '',
    group: 'article',
    icon: 'book',
    items: [
      { name: 'list', title: '', component: 'components/alerts' },
      { name: 'write', title: '', component: 'components/alerts' },
      { name: 'menu', title: '', component: 'components/alerts' },
      { name: 'label', title: '', component: 'components/alerts' }
    ]
  },
  {
    title: '',
    group: 'media',
    icon: 'panorama',
    items: [
      { name: 'list', title: '', component: 'components/alerts' },
      { name: 'write', title: '', component: 'components/alerts' }
    ]
  }
];
// reorder menu
Menu.forEach((item) => {
  if (item.items) {
    item.items.sort((x, y) => {
      const textA = x.title.toUpperCase();
      const textB = y.title.toUpperCase();
      return (textA < textB) ? -1 : (textA > textB) ? 1 : 0;
    });
  }
});

export default Menu;
